<template>
  <div>
    <Button
      title="Очистить приложение"
      :onClick="eraseBase"
    />
  </div>
</template>

<script>

import Button from '../components/Button.vue';

export default {
  name: 'Settings',
  components: {
    Button,
  },
  methods: {
    eraseBase() {
      this.$swal.fire({
        title: 'Вы уверенны?',
        text: 'Очистив приложение вы потеряете данные за все время пользования и сможете начать сначала.',
        icon: 'warning',
        confirmButtonColor: '#ff0000',
        confirmButtonText: 'Да удалить',
      }).then((result) => {
        if (result.value) {
          localStorage.come = JSON.stringify([]);
          localStorage.goal = JSON.stringify([]);
          this.$swal.fire(
            'Удалено!',
            'Ваши данные удалены.',
            'success',
          );
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>

  .settings {
    &__title {
      margin: 0px 0px 15px 0px;
      font-size: 16px;
    }
  }

</style>
