<template>
  <div>
    <div class="input__block">
      <p class="input__title">
        Выберите категорию
      </p>
      <vue-single-select
        v-model="consumptionType"
        class="input"
        :options="consumptions"
        placeholder=""
      ></vue-single-select>
    </div>
    <div class="input__block">
      <p class="input__title">
        Введите сумму
      </p>
      <input
        class="input input--number"
        type="number"
        pattern="[0-9]*"
        v-model="consumption"
      >
    </div>
    <Button
      :onClick="save"
      title="Добавить"
    />
  </div>
</template>

<script>

import config from '@/config';
import Button from '../components/Button.vue';

export default {
  name: 'AddConsumption',
  components: {
    Button,
  },
  data() {
    return {
      consumptionType: '',
      consumption: null,
      consumptions: config.types,
    };
  },
  methods: {
    save() {
      if (!this.consumption || this.consumptionType === '') {
        this.$swal.fire({
          icon: 'error',
          title: 'Ошибка!',
          text: 'Поля категория и сумма должны быть верно заполнены!',
        });
      } else {
        const date = new Date();
        const day = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate();
        const month = date.getMonth() < 9 ? `0${date.getMonth() + 1}` : date.getMonth() + 1;
        const year = date.getFullYear();
        const base = JSON.parse(localStorage.come);
        base.push({
          type: 'consumption',
          category: this.consumptionType,
          price: this.consumption,
          date: `${day}.${month}.${year.toString()}`,
        });
        localStorage.come = JSON.stringify(base);
        this.$router.push({ name: 'Home' });
      }
    },
  },
};
</script>

<style lang="scss" scoped>

  .consumption {
    &__title {
      margin: 0px 0px 15px 0px;
      font-size: 16px;
    }
  }

</style>
