<template>
  <div class="home">
    <div class="card__block">
      <p class="title">
        Здравствуй, {{ first_name }}<br>
        Сегодня {{ date }}
      </p>
      <div class="card__container">
        <div class="card__col">
          <div class="card__block__text">
            <p class="card__text card__text--bold">
              {{ budget }}
            </p>
            <p class="card__text">
              Бюджет
            </p>
          </div>
        </div>
        <div class="card__col">
          <div class="card__block__text">
            <p class="card__text card__text--bold success">
              {{ income }}
            </p>
            <p class="card__text">
              Доход<br>на сегодня
            </p>
          </div>
        </div>
        <div class="card__col">
          <div class="card__block__text">
            <p class="card__text card__text--bold error">
              {{ consumption }}
            </p>
            <p class="card__text">
              Расход<br>на сегодня
            </p>
          </div>
        </div>
      </div>
    </div>
    <div>
      <div
        class="goal__block goal__block--home"
        :class="[goal.consumption > goal.price ? 'goal__block--error' : '']"
        v-for="(goal, key) in goals"
        v-bind:key="key"
      >
        <p>
          {{ goal.category }}
        </p>
        <p
          class="last"
          :class="[goal.consumption > goal.price ? 'error' : 'success']">
          {{ goal.consumption }} / {{ goal.price }}
        </p>
      </div>
    </div>
    <Link
      v-for="(link, key) in links"
      v-bind:key="key"
      :title="link.title"
      :link="link.link"
      :img="link.img"
    />
  </div>
</template>

<script>

import bridge from '@vkontakte/vk-bridge';
import Link from '../components/Link.vue';

export default {
  name: 'Home',
  components: {
    Link,
  },
  data() {
    return {
      links: [
        {
          title: 'Доход',
          link: 'addIncome',
          img: 'arrow-up',
        },
        {
          title: 'Расход',
          link: 'addConsumption',
          img: 'arrow-down',
        },
        {
          title: 'Цели',
          link: 'Goals',
          img: 'mark',
        },
        {
          title: 'Отчет',
          link: 'Chart',
          img: 'chart',
        },
        {
          title: 'Настройки',
          link: 'Settings',
          img: 'settings',
        },
      ],
      income: 0,
      consumption: 0,
      budget: 0,
      goals: [],
      first_name: !localStorage.name ? '' : localStorage.name,
    };
  },
  computed: {
    date() {
      const date = new Date();
      const day = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate();
      const month = date.getMonth() < 9 ? `0${date.getMonth() + 1}` : date.getMonth() + 1;
      const year = date.getFullYear();
      return `${day}.${month}.${year.toString()}`;
    },
  },
  mounted() {
    /* eslint-disable */
    bridge.send('VKWebAppInit');
    bridge.subscribe((e) => {
      if (e.detail.type === 'VKWebAppGetUserInfoResult') {
        if (!localStorage.name) {
          localStorage.name = e.detail.data.first_name;
          this.first_name = e.detail.data.first_name;
        }
      }
      if (e.detail.type === 'VKWebAppUpdateConfig') {
        document.querySelector('body').classList.add(e.detail.data.scheme !== 'bright_light' ? 'client_dark' : 'client_light');
      }
    });
    bridge.send('VKWebAppGetUserInfo', {});
    /* eslint-enabled */
    const comes = JSON.parse(localStorage.come);
    let In = 0;
    let Out = 0;
    comes.forEach((item) => {
      if (item.type === 'income' && item.date === this.date) {
        this.income += parseInt(item.price, 10);
      }
      if (item.type === 'consumption' && item.date === this.date) {
        this.consumption += parseInt(item.price, 10);
      }
      if (item.type === 'income') {
        In += parseInt(item.price, 10);
      }
      if (item.type === 'consumption') {
        Out += parseInt(item.price, 10);
      }
    });
    this.budget = In - Out;
    const goals = JSON.parse(localStorage.goals);
    goals.forEach((item) => {
      let consumption = 0;
      comes.forEach((a) => {
        if (a.type === 'consumption' && a.date === this.date && item.category === a.category) {
          consumption += parseInt(a.price, 10);
        }
      });
      item.consumption = consumption;
    });
    this.goals = goals;
  },
};
</script>
