import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
import Settings from '../views/Settings.vue';
import AddIncome from '../views/AddIncome.vue';
import AddConsumption from '../views/AddConsumption.vue';
import Chart from '../views/Chart.vue';
import Goals from '../views/Goals.vue';
import AddGoals from '../views/AddGoals.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/income',
    name: 'addIncome',
    component: AddIncome,
  },
  {
    path: '/consumption',
    name: 'addConsumption',
    component: AddConsumption,
  },
  {
    path: '/addGoals',
    name: 'addGoals',
    component: AddGoals,
  },
  {
    path: '/goals',
    name: 'Goals',
    component: Goals,
  },
  {
    path: '/chart',
    name: 'Chart',
    component: Chart,
  },
  {
    path: '/setting',
    name: 'Settings',
    component: Settings,
  },
];

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes,
});

export default router;
