      <!-- eslint-enabled -->
    </p>
    <div
      class="goal__block"
      :class="[goal.consumption > goal.price ? 'goal__block--error' : '']"
      v-for="(goal, key) in goals"
      v-bind:key="key"
    >
      <p>
        {{ goal.category }}
      </p>
      <p
        class="last"
        :class="[goal.consumption > goal.price ? 'error' : 'success']">
        {{ goal.consumption }} / {{ goal.price }}
      </p>
      <button
        class="goal__img"
        v-on:click="deleteGoals(key)"
      >
        <svg width="15" height="15" viewBox="0 0 15 15" fill="none" xmlns="http://www.w3.org/2000/svg">
          <g clip-path="url(#clip0)">
