      </button>
    </div>
    <Button
      title="Добавить цель +"
      :onClick="addGoals"
    />
  </div>
</template>

<script>

import Button from '../components/Button.vue';

export default {
  name: 'Goals',
  components: {
    Button,
  },
  data() {
    return {
      goals: [],
    };
  },
  computed: {
    date() {
      const date = new Date();
      const day = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate();
      const month = date.getMonth() < 9 ? `0${date.getMonth() + 1}` : date.getMonth() + 1;
      const year = date.getFullYear();
      return `${day}.${month}.${year.toString()}`;
    },
  },
  mounted() {
    /* eslint-disable */
    const comes = JSON.parse(localStorage.come);
    const goals = JSON.parse(localStorage.goals);
    goals.forEach((item) => {
      let consumption = 0;
      comes.forEach((a) => {
        if (a.type === 'consumption' && a.date === this.date && item.category === a.category) {
          consumption += parseInt(a.price, 10);
        }
      });
      item.consumption = consumption;
    });
    this.goals = goals;
    /* eslint-enabled */
  },
  methods: {
    getInfo() {
      this.$swal.fire({
        showConfirmButton: false,
        html: 'Добавьте цель, для того что бы следить за расходами по нужной категории в течении дня.<br><br>При превышении лимита цель подсветиться красным.',
      });
    },
    deleteGoals(key) {
      this.goals.splice(key, 1);
      localStorage.goals = JSON.stringify(this.goals);
    },
    addGoals() {
      this.$router.push({ name: 'addGoals' });
    },
  },
};
</script>
