<template>
  <div>
    <div v-if="!result">
      <div class="chart__row">
        <div class="chart__col chart__col--full">
          <div class="chart__button__block">
            <button
              v-on:click="setDate(0)"
              class="chart__button">
              День
            </button>
          </div>
          <div class="chart__button__block">
            <button
              v-on:click="setDate(7)"
              class="chart__button">
              Неделю
            </button>
          </div>
          <div class="chart__button__block">
            <button
              v-on:click="setDate(30)"
              class="chart__button">
              Месяц
            </button>
          </div>
          <div class="chart__button__block">
            <button
              v-on:click="setDate(183)"
              class="chart__button">
              Полгода
            </button>
          </div>
        </div>
        <div class="chart__col">
          <p class="input__title">
            От:
          </p>
          <date-picker
            v-model="dateIn"
            valueType="format"
            class="input input--date"
          ></date-picker>
        </div>
        <div class="chart__col">
          <p class="input__title">
            До:
          </p>
          <date-picker
            v-model="dateOut"
            valueType="format"
            class="input input--date"
          ></date-picker>
        </div>
      </div>
      <Button
        title="Показать"
        :onClick="showChart"
      />
    </div>
    <div v-else>
      <Button
        title="Изменить дату"
        :onClick="changeDate"
      />
      <div class="chart__over" v-if="resultArr.length > 0">
        <div
          v-for="(item, key) in resultArr"
          v-bind:key="key"
          class="chart__block"
          :class="[item.type === 'consumption' ? 'error' : 'success']"
        >
          <p>
            {{ item.category }}
          </p>
          <p>
            {{ item.price }}
          </p>
          <p>
            {{ item.date }}
          </p>
          <span
            v-if="item.type === 'consumption'"
            class="chart__img">
            <svg width="25" height="25" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg">
              <g clip-path="url(#clip0)">
                <path d="M12.5058 16.9117L1.64191 6.04784C1.26628 5.67227
                  0.657286 5.67227 0.281654 6.04784C-0.0938642
                  6.42353 -0.0938642 7.03247 0.281654 7.4081L11.8257
                  18.9521C12.2013 19.3276 12.8103 19.3276 13.1859
                  18.9521L24.7299 7.4081C25.099 7.02593 25.0884
                  6.41693 24.7063 6.04784C24.3334 5.68782
                  23.7424 5.68782 23.3697
                  6.04784L12.5058 16.9117Z"
                  fill="black"/>
              </g>
              <defs>
                <clipPath id="clip0">
                  <rect y="25" width="25" height="25" transform="rotate(-90 0 25)" fill="white"/>
                </clipPath>
              </defs>
            </svg>
          </span>
          <span
            v-else
            class="chart__img">
            <svg width="25" height="25" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg">
              <g clip-path="url(#clip0)">
                <path d="M12.4942 8.08827L23.3581 18.9522C23.7337 19.3277
                  24.3427 19.3277 24.7183 18.9522C25.0939 18.5765
                  25.0939 17.9675 24.7183 17.5919L13.1743 6.04789C12.7987
                  5.67237 12.1897 5.67237 11.8141 6.04789L0.270073
                  17.5919C-0.0990194 17.9741 -0.0884217 18.5831
                  0.293748 18.9522C0.666561 19.3122 1.25757
                  19.3122 1.63033 18.9522L12.4942 8.08827Z"
                  fill="black"/>
              </g>
              <defs>
                <clipPath id="clip0">
                  <rect x="25" width="25" height="25" transform="rotate(90 25 0)" fill="white"/>
                </clipPath>
              </defs>
            </svg>
          </span>
        </div>
      </div>
      <div v-else>
        <p>
          Нет данных за выбранный период!
        </p>
      </div>
    </div>
  </div>
</template>

<script>

import DatePicker from 'vue2-datepicker';
import 'vue2-datepicker/index.css';
import Button from '../components/Button.vue';

export default {
  name: 'Chart',
  components: {
    Button,
    DatePicker,
  },
  data() {
    return {
      dateIn: '',
      dateOut: '',
      resultArr: [],
      result: false,
    };
  },
  methods: {
    changeDate() {
      this.resultArr = [];
      this.result = false;
      this.dateIn = '';
      this.dateOut = '';
    },
    setDate(type) {
      const date = new Date();
      const day = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate();
      const month = date.getMonth() < 9 ? `0${date.getMonth() + 1}` : date.getMonth() + 1;
      const year = date.getFullYear();
      const dateO = new Date();
      dateO.setTime(dateO.getTime() - 3600 * 1000 * 24 * type);
      const dayO = dateO.getDate() < 10 ? `0${dateO.getDate()}` : dateO.getDate();
      const monthO = dateO.getMonth() < 9 ? `0${dateO.getMonth() + 1}` : dateO.getMonth() + 1;
      const yearO = dateO.getFullYear();
      this.dateIn = `${year}-${month}-${day}`;
      this.dateOut = `${yearO}-${monthO}-${dayO}`;
      this.showChart();
    },
    showChart() {
      if (this.dateIn === '' || this.dateOut === '') {
        this.$swal.fire({
          icon: 'error',
          title: 'Ошибка!',
          text: 'Дата от и до должны быть верно заполнены!',
        });
      } else {
        const comes = JSON.parse(localStorage.come);
        let cache = this.dateIn.split('-');
        const dateInFormat = `${cache[2]}.${cache[1]}.${cache[0]}`;
        cache = this.dateOut.split('-');
        const dateOutFormat = `${cache[2]}.${cache[1]}.${cache[0]}`;
        cache = [];
        comes.forEach((i) => {
          if (i.date >= dateInFormat && i.date <= dateOutFormat) {
            cache.push(i);
          }
        });
        this.result = true;
        this.resultArr = cache;
      }
    },
  },
};
</script>

