<template>
  <div>
    <div class="head__block">
      <transition>
        <Back v-show="thisPage !== 'Home'" />
      </transition>
      <span class="head__title">
        {{ pageName }}
      </span>
    </div>
    <router-view/>
  </div>
</template>

<script>

import Back from '@/components/Back.vue';

export default {
  name: 'App',
  components: {
    Back,
  },
  computed: {
    thisPage() {
      return this.$route.name;
    },
    pageName() {
      if (this.$route.name === 'Home') {
        return 'Главная';
      }
      if (this.$route.name === 'addIncome') {
        return 'Добавить доход';
      }
      if (this.$route.name === 'addConsumption') {
        return 'Добавить расход';
      }
      if (this.$route.name === 'Goals') {
        return `Цели на ${this.date}`;
      }
      if (this.$route.name === 'addGoals') {
        return 'Добавить цель';
      }
      if (this.$route.name === 'Chart') {
        return 'Отчет';
      }
      if (this.$route.name === 'Settings') {
        return 'Настройки';
      }
      return 'Ошибка';
    },
    date() {
      const date = new Date();
      const day = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate();
      const month = date.getMonth() < 9 ? `0${date.getMonth() + 1}` : date.getMonth() + 1;
      const year = date.getFullYear();
      return `${day}.${month}.${year}`;
    },
  },
  mounted() {
    if (!localStorage.come) {
      localStorage.come = JSON.stringify([]);
    }
    if (!localStorage.goals) {
      localStorage.goals = JSON.stringify([]);
    }
  },
};
</script>

